.about-us {
  @include pageGrid;

  &__title {
    margin-bottom: 32px;
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 1 / 5;
    }
  }

  &__text {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 6 / 12;
    }
  }
}
